import {Component} from 'react';
import AddTaskForm from './AddTaskForm';
import TaskTable from './TaskTable';
import { Route, Link } from 'react-router-dom';

class ToDoApp extends Component {
    state = {
        tasks: [
            {name: 'eat', status: 'done'},
            {name: 'code', status: 'done'},
            {name: 'sleep', status: 'done'},
        ]
    }

    // how to pass data from child to parent?
    // 1 create a function sa parent
    // 2 pass the function to child (via props)
    // 3 get data by invoking the function in child

    save = (name) => {
        let status = 'pending';
        let newTask = {
            name,
            status
        }

        let tasksCopy = [newTask, ...this.state.tasks];
        this.setState({
            tasks: tasksCopy
        })
    }

    render() {
        let pendingTasks = this.state.tasks.filter( task => task.status === 'pending');
        let doneTasks = this.state.tasks.filter( task => task.status === 'done');

        return(
            <div>
                <h1>To Do App</h1>
                <Link to="/todo/add">Add Task</Link> <br/>
                <Link to="/todo/pending">Pending Tasks</Link> <br/>
                <Link to="/todo/done">Done Task</Link> <br/>

                <Route path="/todo/add">
                    <AddTaskForm save={this.save} />
                </Route>
                <Route path="/todo/pending">
                    <TaskTable tasks={pendingTasks} status='Pending' />
                </Route>
                <Route exact path="/todo/done">
                    <TaskTable tasks={doneTasks} status='Done' />
                </Route>
            </div>
        );
    }
}

export default ToDoApp;