import { Component } from "react";
import ItemBox from "./ItemBox";
import { Route, Link } from 'react-router-dom';

class RestoApp extends Component {
    state = {
        items: [
            {
                id: 1,
                name: "Burger",
                price: 50,
                category: "Food",
                status: 'active',
                image: "https://image.flaticon.com/icons/svg/1046/1046784.svg"
            },
            {
                id: 2,
                name: "Pizza",
                price: 100,
                category: "Food",
                status: 'active',
                image: "https://image.flaticon.com/icons/svg/1046/1046771.svg"
            },
            {
                id: 3,
                name: "Fries",
                price: 25,
                category: "Food",
                status: 'active',
                image: "https://image.flaticon.com/icons/svg/1046/1046786.svg"
            },
            {
                id: 4,
                name: "Coffee",
                price: 35,
                category: "Drink",
                status: 'active',
                image: "https://image.flaticon.com/icons/svg/1046/1046785.svg"
            },
            {
                id: 5,
                name: "Iced Tea",
                price: 45,
                category: "Drink",
                status: 'active',
                image: "https://image.flaticon.com/icons/svg/1046/1046782.svg"
            },
            {
                id: 6,
                name: "Hot Tea",
                price: 45,
                category: "Drink",
                status: 'active',
                image: "https://image.flaticon.com/icons/svg/1046/1046792.svg"
            },
        ],
        category: 'All',
        cart: [
            {
                id: 1,
                quantity: 1,
                name: "Burger",
                price: 50,
                category: "Food",
                status: 'active',
                image: "https://image.flaticon.com/icons/svg/1046/1046784.svg"
            },
            {
                id: 3,
                quantity: 1,
                name: "Fries",
                price: 25,
                category: "Food",
                status: 'active',
                image: "https://image.flaticon.com/icons/svg/1046/1046786.svg"
            },
        ]
    }

    categoryChangeHandler = (e) => {
        this.setState({
            category: e.target.value
        })
    }

    render() {
        // let foodItems = this.state.items.filter( item => item.category === 'Food' );
        // let drinkItems = this.state.items.filter( item => item.category === 'Drink' );
        let display = this.state.items;
        if(this.state.category !== 'All') {
            display = this.state.items.filter( item => item.category === this.state.category );
        }

        return(
            <div>
                {this.state.category}
                <select value={this.state.category} onChange={this.categoryChangeHandler}>
                    <option>All</option>
                    <option>Food</option>
                    <option>Drink</option>
                </select>
                {
                    display.map( item => {
                        return(
                            <ItemBox item={item} />
                        )
                    })
                }
            </div>
        );
    }
}

export default RestoApp;